import React from 'react';
import styles from './InfoContainerReverse.module.css';


const InfoContainerRevers = (props) => {
    return (
        <div className={styles.info}>
            <div className={styles.infoContainer}>
                <div className={styles.bottomContainer}>
                    <div className={styles.imgContainer}></div>
                    <div className={styles.textContainer}>
                        <p className={styles.text}>:  12   2 .    
                            .</p>
                        <p className={styles.text}>
                                 :<br/>
                            1.   <br/>
                            2., <br/>
                            3.<br/>
                                .</p>
                    </div>
                </div>
                <div className={props.show ? styles.topContainer : `${styles.topContainer} ${styles.move}`}>
                    <div className={styles.topTextContainer}>
                        <p className={styles.firstText}> </p>
                        <p className={styles.secondText}>  </p>
                    </div>
                    <div className={styles.bottomTextContainer}>
                        <p>:  12   2 .<br/> * :     ,.
                            *<br/>      <br/>  , 
                            .<br/> *  :<br/>   ,<br/>  .<br/>
                                  .<br/>  : 2   
                            (  <br/>  )</p>
                    </div>
                    <div>
                        <button className={styles.btnTopContainer}></button>
                    </div>
                </div>
            </div>
            <button className={props.show? styles.btn :`${styles.btn} ${styles.btnMove}`} onClick={() => props.setShow(!props.show)}>{!props.show? '' : ''}</button>
            <div>
            </div>

        </div>
    );
};

export default InfoContainerRevers;